import React from 'react';

class NewsletterSignup extends React.Component {
	render() {
		return (
			<div className="NewsletterSignup border border-box__underneath">
				<div className="NewsletterSignup__inner border__bottom">
					<div className="textInput">
						<input 
							className="textField"
							type="email"
							placeholder="Enter your email to signup for our newsletter" />
					</div>
					<div className="submitButton">
						<input 
							className="textField"
							type="submit"
							value="" />
					</div>
				</div>
			</div>
		)
	}
}

export default NewsletterSignup;


