import React, { Component } from 'react';
import Header from '../header/Header';
import NewsletterSignup from '../newsletterSignup/NewsletterSignup';
import Footer from '../footer/Footer';
import SocialIcons from '../../types/socialTypes';

class App extends Component {
  constructor() {
    super();
    this.state = {
      social: SocialIcons
    };
  }

  render() {
    return (
      <div className="App border__bottom">
        <Header />
        <main className="site__body">
        	{this.props.children}
        </main>
        <NewsletterSignup />
        <Footer social={this.state.social} />
      </div>
    );
  }
}

export default App;





