import React from 'react';
import HomepageSlider from '../components/homepageSlider/HomepageSlider';
import HomepageTextBlock from '../components/homepageTextBlock/HomepageTextBlock';
import HomepageHoverBlock from '../components/homepageHoverBlock/HomepageHoverBlock';
import HomepageFeatureBlock from '../components/homepageFeatureBlock/HomepageFeatureBlock';
import HomepageCourses from '../components/homepageCourses/HomepageCourses';
import hpBox1 from '../shared/images/hpBox1.jpg';
import hpBox2 from '../shared/images/hpBox2.jpg';
import hpBox3 from '../shared/images/hpBox3.jpg';
import hpBox4 from '../shared/images/hpBox4.jpg';

import Courses from '../types/courseType'

class Homepage extends React.Component {
	constructor() {
		super();
		// getInitialState
		this.state = {
			courses: Courses
		};
	}

	render() {
		return (
			<div>
				<HomepageSlider />
				<HomepageTextBlock
					desc="Aspire is the epitome of a contemporary fitness company in Australia. A blend of cutting edge fitness techniques with a fashionable modern lifestyle. " />

				<div className="HomepageHoverBlock">
					<HomepageHoverBlock 
						hbImage={hpBox1}
						hbTitle="Performance"
						hbDesc="ZUU & Ankorr Training centres around primal movement patterns & bodyweight strength through range. It bridges the gap between hardcore Strength & Conditioning programs."
						hbLink="/what-we-do"
						hbColour="rgba(116, 226, 225, 1)" />
					<HomepageHoverBlock 
						hbImage={hpBox2}
						hbTitle="Recovery"
						hbDesc="ZUU & Ankorr Training centres around primal movement patterns & bodyweight strength through range. It bridges the gap between hardcore Strength & Conditioning programs."
						hbLink="/what-we-do"
						hbColour="rgba(178, 221, 74, 1)" />
					<HomepageHoverBlock 
						hbImage={hpBox3}
						hbTitle="Personal Training"
						hbDesc="ZUU & Ankorr Training centres around primal movement patterns & bodyweight strength through range. It bridges the gap between hardcore Strength & Conditioning programs."
						hbLink="/what-we-do"
						hbColour="rgba(26, 159, 225, 1)" />
					<HomepageHoverBlock 
						hbImage={hpBox4}
						hbTitle="Group Training"
						hbDesc="ZUU & Ankorr Training centres around primal movement patterns & bodyweight strength through range. It bridges the gap between hardcore Strength & Conditioning programs."
						hbLink="/what-we-do"
						hbColour="rgba(246, 217, 197, 1)" />
				</div>

				<HomepageTextBlock
					desc="Aspire is the epitome of a contemporary fitness company in Australia. A blend of cutting edge fitness techniques with a fashionable modern lifestyle. " />

				<HomepageFeatureBlock 
					fbTitle="Aspire Kids"
					fbDesc="ZUU & Ankorr Training centres around primal movement patterns & bodyweight strength through range. It bridges the gap between hardcore Strength & Conditioning programs."
					fbLink="/what-we-do" />

				<HomepageCourses
					courses={this.state.courses} />

			</div>
		)
	}
}

export default Homepage;




