import React from 'react';
import { Link } from 'react-router';

class HomepageCoursesSlide extends React.Component {
	render() {
		return (
			<div className="HomepageCoursesSlide border__right">
				<Link to={this.props.details.link}>
					<div className="HomepageCoursesSlide__details">
						<p className="name">{this.props.details.name}</p>
						<p className="day">{this.props.details.day}, <span className="location">{this.props.details.location}</span></p>
					</div>
					<img src={this.props.details.image} alt="An upcoming course from Aspire"/>
				</Link>
			</div>
		)
	}
}

export default HomepageCoursesSlide;


