import React from 'react';
import { Link } from 'react-router';

class SocialIcon extends React.Component {
	render() {
		return (
			<li>
				<Link to={this.props.details.link}><img src={this.props.details.image} alt=""/></Link>
			</li>
		)
	}
}

export default SocialIcon;


