import React from 'react';
import { Link } from 'react-router';
import SocialIcon from '../socialIcon/SocialIcon';

class Footer extends React.Component {
	render() {
		return (
			<div>
				<footer className="footer border__left border__right border-box__underneath">
					<div className="footer--row__top">
						<div className="footer--row__top__item footer__list">
							<ul>
								<li><Link to="/what-we-do">What We Do</Link></li>
								<li><Link to="/what-we-do">Classes</Link></li>
								<li><Link to="/what-we-do">Timetable</Link></li>
								<li><Link to="/what-we-do">Locations</Link></li>
								<li><Link to="/what-we-do">Our Team</Link></li>
							</ul>
						</div>

						<div className="footer--row__top__item footer__list">
							<ul>
								<li><Link to="/what-we-do">About Aspire</Link></li>
								<li><Link to="/what-we-do">Contact</Link></li>
								<li><Link to="/what-we-do">Careers</Link></li>
								<li><Link to="/what-we-do">Equiptment</Link></li>
							</ul>
						</div>

						<div className="footer--row__top__item footer__list medium-down--hide">
							<ul>
								<li>Aspire Group</li>
								<li>1 Pearson Way</li>
								<li>Osbourne Park, 6017</li>
							</ul>
						</div>
						<div className="footer--row__top__item footer__social">
							<ul>
								{ 
									Object
										.keys(this.props.social)
										.map(key => <SocialIcon 
														key={key}
														index={key}
														details={this.props.social[key]} />)
								}
							</ul>
						</div>
					</div>
					<div className="footer-row__bottom small--hide">
						<p>Copyright 2016, Aspire Group LTD.</p>
					</div>
				</footer>
			</div>
		)
	}
}

export default Footer;


