import React from 'react';
import logoLong from '../../shared/images/logo-long.svg'

class HeaderLogo extends React.Component {
	render() {
		return (
			<div className="header--block border__right">
				<img src={logoLong} className="header--block__item header__logo" alt="Aspire Group Logo" />
			</div>
		)
	}
}

export default HeaderLogo;


