import React from 'react';
import { Link } from 'react-router';
import FeatureBlockSlider from '../featureBlockSlider/FeatureBlockSlider';
import arrowNext from '../../shared/images/arrowNext.svg';

class HomepageFeatureBlock extends React.Component {
	render() {
		return (
			<div className="HomepageFeatureBlock border border-box__underneath">
				<FeatureBlockSlider />
				<div className="HomepageFeatureBlock__info-wrapper HomepageFeatureBlock__flex">
					<div className="HomepageFeatureBlock__info">
						<h2>{this.props.fbTitle}</h2>
						<p className="body__small">{this.props.fbDesc}</p>
						<Link to={this.props.fbLink} className="body__small">Discover more <span><img src={arrowNext} className="link__arrow" alt="Discover more"/></span></Link>
					</div>
				</div>
			</div>
		)
	}
}

export default HomepageFeatureBlock;


