import React from 'react';
import Slider from 'react-slick';
import arrowPrev from '../../shared/images/arrowPrev.svg';
import arrowNext from '../../shared/images/arrowNext.svg';
import slide1 from '../../shared/images/slide1.jpg';
import slide2 from '../../shared/images/slide2.jpg';
import slide3 from '../../shared/images/slide3.jpg';


class HomepageSlider extends React.Component {
	constructor(props) {
		super(props)
		this.next = this.next.bind(this)
		this.previous = this.previous.bind(this)
	}

	next() {
		this.slider.slickNext()
	}

	previous() {
		this.slider.slickPrev()
	}

	render() {
		const settings = {
			infinite: true,
			speed: 500,
			slidesToShow: 1,
			slidesToScroll: 1
		}
		const slide1Style = { backgroundImage: 'url(' + slide1 + ')' }
		const slide2Style = { backgroundImage: 'url(' + slide2 + ')' }
		const slide3Style = { backgroundImage: 'url(' + slide3 + ')' }

		return (
			<div className="homepageSlider slider border border-box__underneath">
				<Slider ref={c => this.slider = c } {...settings}>
					<div key={1}><a href="http://google.com"><div className="slide__item" style={slide1Style}></div></a></div>
					<div key={2}><a href="http://google.com"><div className="slide__item" style={slide2Style}></div></a></div>
					<div key={3}><a href="http://google.com"><div className="slide__item" style={slide3Style}></div></a></div>
				</Slider>
				<div className="slider-arrows slider-arrows__bottom-right border background__white border-box__above border-box__left">
					<div className="slider-arrow__item border__right" onClick={this.previous}><img src={arrowPrev} alt="Arrow to move slider back one slide"/></div>
					<div className="slider-arrow__item" onClick={this.next}><img src={arrowNext} alt="Arrow to move slider forward one slide"/></div>
				</div>
			</div>
		)
	}
}

export default HomepageSlider;


