import React from 'react';

class HomepageTextBlock extends React.Component {
	render() {
		return (
			<div className="border hompageTextBlock border-box__underneath">
				<span>{this.props.desc}</span>
			</div>
		)
	}
}

export default HomepageTextBlock;


