import React from 'react';
import { Link } from 'react-router';
import arrowNext from '../../shared/images/arrowNext.svg';
import cx from 'classnames';

class HomepageHoverBlock extends React.Component {
	constructor() {
		super();
		this.handleMouseOver = this.handleMouseOver.bind(this);
		this.handleMouseOut = this.handleMouseOut.bind(this);
		// getInitialState
		this.state = {
			isHover: false
		};
	}

	handleMouseOver () {
		this.setState({ isHovering: true });
	}

	handleMouseOut () {
		this.setState({ isHovering: false });
	}

	render() {
		const imageURL = this.props.hbImage;
		const hbStyle = {
			backgroundImage: 'url(' + imageURL + ')'
		}
		const titleColour = this.props.hbColour;
		const titleColourStyle = {
			backgroundColor: titleColour
		}
		const infoClasses = cx([
	      this.state.isHovering && 'hover',
	      'HomepageHoverBlock__info',
	      'border__top',
	      'background__white'
	    ]);

		return (
			<div className="HomepageHoverBlock__item border border-box__underneath" style={hbStyle}>
				<Link to={this.props.hbLink}>
					<div 
						className={cx(infoClasses)}
						onMouseOver={this.handleMouseOver}
        				onMouseOut={this.handleMouseOut} >
						<div className="HomepageHoverBlock__title">
							<h3 className="HomepageHoverBlock__title--item"><span className="HomepageHoverBlock__title--colour" style={titleColourStyle}></span>{this.props.hbTitle}</h3>
							<img className="HomepageHoverBlock__title--item" src={arrowNext} alt="Link to What we Do"/>
						</div>
						<div className="HomepageHoverBlock__desc">
							<p className="body__small">{this.props.hbDesc}</p>
						</div>
					</div>
				</Link>
			</div>
		)
	}
}

export default HomepageHoverBlock;


