import React from 'react';
import HeaderLogo from '../headerLogo/HeaderLogo';
import HeaderNav from '../headerNav/headerNav';
import NavLinkList from '../navLinkList/NavLinkList';


class Header extends React.Component {
	constructor() {
		super();
		this.toggleMobileNav = this.toggleMobileNav.bind(this);
		// getInitialState
		this.state = {
			isOpen: false
		};
	}

	toggleMobileNav() {
		this.setState({isOpen: !this.state.isOpen});
		console.log('Something happened');
	}

	render() {
        const toggleClass = this.state.isOpen ? "isOpen" : "";
        const className = 'large--hide header--nav__dropdown border__left border__right border__bottom background__white ' + toggleClass;

		return (
			<div className="header__container background__white">
				<header className="header border">
					<HeaderLogo />
					<HeaderNav />
					<div className="header--block large--hide header__nav--mobile">
						<p className="nav--mobile__trigger" onClick={this.toggleMobileNav}>Menu<span></span></p>
					</div>
				</header>
				<div className={className}>
					<NavLinkList />
				</div>

			</div>
		)
	}
}

export default Header;


