import React from 'react';
import { Link } from 'react-router';

class NavLinkList extends React.Component {
	render() {
		return (
			<ul>
				<li className="header__nav--item"><Link to="/what-we-do">What We Do</Link></li>
				<li className="header__nav--item"><Link to="/what-we-do">Classes</Link></li>
				<li className="header__nav--item"><Link to="/what-we-do">Timetable</Link></li>
				<li className="header__nav--item"><Link to="/what-we-do">Locations</Link></li>
				<li className="header__nav--item"><Link to="/what-we-do">Our Team</Link></li>
			</ul>
		)
	}
}

export default NavLinkList;


