import React from 'react';
import { Link } from 'react-router';
import Slider from 'react-slick';
import arrowNext from '../../shared/images/arrowNext.svg';
import arrowPrev from '../../shared/images/arrowPrev.svg';
import HomepageCoursesSlide from '../homepageCoursesSlide/HomepageCoursesSlide'


class HomepageCourses extends React.Component {
	render() {
		const settings = {
			infinite: true,
			speed: 500,
			slidesToShow: 5,
			slidesToScroll: 1,
			arrows: true,
			nextArrow: <div className="slider-arrow__item border"><img src={arrowNext} alt="Arrow to move slider forward one slide"/></div>,
			prevArrow: <div className="slider-arrow__item border"><img src={arrowPrev} alt="Arrow to move slider back one slide"/></div>,
			responsive: [
			    {
			      breakpoint: 900,
			      settings: {
			        slidesToShow: 3
			      }
			    },
			    {
			      breakpoint: 485,
			      settings: {
			        slidesToShow: 1
			      }
			    }
			  ]
		}
		return (
			<div className="HomepageCourses border border-box__underneath">
				<div className="HomepageCourses__title border__bottom">
					<h3>Upcoming Classes</h3>
					<Link to="/what-we-do" className="HomepageCourses__link">
						<p className="body__small">See All Classes</p>
						<img src={arrowNext} alt="" className="link__arrow"/>
					</Link>
				</div>
				<div className="HomepageCourses__slider">
					<Slider {...settings}>
						{
							Object
								.keys(this.props.courses)
								.map(key => <div key={key}><HomepageCoursesSlide key={key} details={this.props.courses[key]} /></div>)
						}
					</Slider>
				</div>
			</div>
		)
	}
}

export default HomepageCourses;


